*{
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body,html{
  width:100%;
  height:100%;
  padding:0;
}
ul{
  margin:0;
  padding:0;
  list-style: none;
}
a{
  text-decoration: none;
  color:black;
}
#root{
  width:100%;
  height:100%;
}
.App{
  width:100%;
  height:100%;
}
/**** Header작업 ****/
header{
  width:100%;
  height:10%;
}
/*** nav 작업 ***/
.nav_ul{
  display:flex;
}
.nav_ul li{
  margin:5px;
  padding:15px;
  border-radius:20px;
  border:1px solid #ddd;
  font-weight: bold;
}
.nav_ul li.active{
  background-color: aqua;
}
/**** Contents작업 ****/
.lst_menu{
  margin-top:10px;
}
.tbl_name{
  padding-left:5px;
  font-size: 1.5rem;
  width:100%;
  margin-bottom: 10px;
  background:skyblue;
  padding-top:5px;
  padding-bottom:5px;
}
.tbl_ul{
  display:flex;
  flex-wrap: wrap;
}
.tbl_ul li{
  position: relative;
  border:1px solid #ddd;
  width:calc(25% - 10px);
  height:100px;
  margin:5px;
  cursor: pointer;
  font-size:25px;
  text-align: center;
}
.tbl_li span{
  font-size: 1rem;
}
.tbl_li div{
  width:100%;
  position: absolute;
  opacity: .8;
  color:white;
  background: black;
  text-align: center;
  bottom:0px;
}
/**** footer작업 ****/
footer{
  display:flex;
  position: fixed;
  bottom: 0;
  width:100%;
  height:30%;
  border:1px solid #ddd;
}
footer ul{
  width:90%;
  display:flex;
  flex-wrap:wrap;
}
footer .total{
  position: relative;
  width:10%;
  font-size: 2rem;
}
footer .total span{
  position: absolute;
  top:calc(50% - 16px);
}
footer li{
  position: relative;
  border:1px solid #ddd;
  width:calc(15% - 10px);
  height:calc(50% - 10px);
  margin:5px;
  cursor: pointer;
  font-size:1.2rem;
  text-align: center;
}
footer li div{
  font-size:1rem;
}
footer li{
  position: relative;
}
footer li .Count_box{
  position: absolute;
  display:flex;
  width:100%;
  justify-content: center;
  bottom:0;
}
footer li .trash{
  position: absolute;
  right:5px;
  top:5px;
}
footer li div.price{
  padding-top:10px;
}
footer li div span{
  display:block;
  width:20px;
  height:20px;
  margin:7px;
  border:1px solid black;
}
footer .total span{
  font-size: .6rem;
}
footer li div span:hover:not(.Count){
  background:skyblue;
}